<template>
    <div>
        <p>
            As a UK based printer enthusiast, you can use this form to contact me with your 3D printing questions or commisions.
        </p>
        <p>
            If you add in as much detail as you can and I'll get back to you as soon as I can. Also this form doesn't work currently. If you do want to contact me use twitter
        </p>
        <MaterialInput
            inputName="email"
            label="E-mail"
            :valueFunction="formUpdate"
            commit="setContactEmail"
            :validationFunction="emailValid"
            validationMessage="I can't E-Mail you if you don't put in a valid email address"
        ></MaterialInput>
        <MaterialInput
            inputName="name"
            label="Name"
            :valueFunction="formUpdate"
            commit="setContactName"
            :validationFunction="nameValid"
            validationMessage="I won't know what to call you if you leave this blank"></MaterialInput>
        <MaterialInput
            inputName="details"
            label="Details"
            :multiline="true"
            :lines="15"
            :valueFunction="formUpdate"
            commit="setContactMessage"
            :validationFunction="messageValid"
            validationMessage="I can't read minds so won't know what you want from me"></MaterialInput>
        <div class="button-holder" v-show="isValid && formStarted">
            <a class="button" @click="executeRecaptcha" :disabled="!isValid">Submit</a>
        </div>
        <Recaptcha ref="recaptcha" @verify="submitted"></Recaptcha>
    </div>
</template>

<script>
export default {
  name: 'contact-form',
  data: function () {
    return {
      validationResults: {
        email: false,
        name: false,
        message: false
      },
      formStarted: false
    }
  },
  computed: {
    isValid: function () {
      return this.validationResults.email && this.validationResults.name && this.validationResults.message
    }
  },
  methods: {
    executeRecaptcha: function () {
      if (this.isValid) {
        this.$refs.recaptcha.execute()
      }
    },
    submitted: function (response) {
      this.$emit('submitted', this.formInput)
    },
    formUpdate: function (commit, value) {
      this.$store.commit(commit, { item: value })
    },
    emailValid: function (value) {
      // eslint-disable-next-line
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

      var validationResults = re.test(String(value).toLowerCase())
      this.validationResults.email = validationResults
      this.formStarted = true

      return validationResults
    },
    nameValid: function (value) {
      var validationResults = value !== ''
      this.validationResults.name = validationResults
      this.formStarted = true

      return validationResults
    },
    messageValid: function (value) {
      var validationResults = value !== ''
      this.validationResults.message = validationResults
      this.formStarted = true

      return validationResults
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .contact-form {
        display: flex;
        flex-direction: column;

        .button-holder {
            margin-top: 25px;
            margin-left: auto;
            margin-right: auto;
            width: 75%;
            text-align: right;

            a {
                &.button {
                    margin-left: auto;
                    text-decoration: none;
                    padding: 5px 15px;
                    border: 1px solid $primaryColour;
                    background-color: $primaryColour;
                    width: 150px;
                    cursor: pointer;
                    border-radius: 3px;
                }

                &:hover {
                    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                }
            }
        }
    }
</style>



